@import '../assets/variables';

.about-content {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
}

.about-content__div__1 {
    background-color: $color-Pri;
    height: 100vh;
    width: inherit;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 50px;
    border-left: 5px solid $color-the; 
    border-radius: 20px;
    position: relative;
    overflow: hidden;
    color: $color-sec; /* Assuming you have a text color variable */
}

.about-content__div__1::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(to right, rgba(255,255,255,0), rgba(255,255,255,0.8));
    transform: translateX(-100%);
    transition: transform 0.5s ease-in-out;
}

.about-content__div__1:hover::before {
    transform: translateX(0);
}

.about-content__div__1 p {
    margin: 0;
    padding: 0;
    font-size: 1.8rem; /* Adjust the font size as needed */
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 1.2px;

}

.about-text__1p {
    font-size: 1.2rem; /* Adjust based on your design preference */
    line-height: 1.6;
    padding-top: 20px;
    animation: fadeIn 1.5s ease-in-out;
}

.about-content__div__2 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-left: 5px solid $color-the; /* Accent color */
    position: relative;
    overflow: hidden;
    background-color: $color-Pri;
    margin-top: 30px;
    border-radius: 20px;
    box-shadow: 0 4px 8px rgba(0,0,0,0.1);
    transition: transform 0.3s ease-in-out;
    width: fit-content;
}

.about-content__div__2__p:hover {
    transform: translateY(-5px); /* Slight lift effect on hover */
}

.about-content__div__2__p {
    font-size: 1.8rem; /* Adjust the font size as needed */
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 1.2px;
    margin: 0;
    margin: 10px;
    padding: 0;
    border-radius: 10px;
    text-align: center;
    padding: 50px;
    animation: fadeIn 1.5s ease-in-out;
    color: $color-sec;

}

.about__grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    background-color: $color-Pri; 
}

.projects {
    border: 2px solid $color-the; 
    border-radius: 10px;
    overflow: hidden;
    // min-width: 280px; 
    width: 45%;
    margin: 10px;
}

.projects:hover {
    box-shadow: 0 6px 12px rgba(0,0,0,0.15); 
}

@media (max-width: 768px) {
    .projects {
        width: 50%; 
        // border: dotted red 5px;
    }
}



@media (max-width: 480px) {
    .about-content__div__2__p {
        font-size: 1.2rem;
    }
}

@media (max-width: 768px) {
    .about__grid {
        display: flex;
        flex-direction: column;
        justify-content: center;
        // align-items: center;
        width: 200%;
    }
    
    .about-content__div__2 {
        width: 100%;
        margin-top: 20px;
        padding: 20px;
    }
}

